@font-face {
  font-family: "Trebuchet MS";
  src: url("../fonts/trebuchet-ms.woff2") format("woff2"), url("../fonts/trebuchet-ms.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "Exo2.0";
  src: url("../fonts/exo20-thin.woff2") format("woff2"), url("../../fonts/exo20-thin.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  scroll-behavior: smooth;
  overflow-x: hidden;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Trebuchet MS", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  min-width: 320px;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1366px;
  margin: 0 auto;
  padding: 0 80px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 20px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 14px;
  }
}

.header h2 {
  text-align: center;
}

.footer h2 {
  text-align: center;
}

.main {
  display: flex;
}

.main__wrapper {
  display: flex;
  flex-grow: 1;
  border: 5px solid #666666;
}

.form {
  padding: 10px 0 0 0;
  flex-grow: 1;
}

.form__search {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  border-bottom: 5px solid #666666;
}

.form__search input {
  display: inline-block;
  vertical-align: middle;
  margin: 0 0 10px;
  padding: 5px;
  width: 100%;
  max-width: 290px;
  border-radius: 12px;
}

.form__table {
  margin: 0 0 20px 0;
  display: grid;
  grid-template-columns: minmax(150px, 1fr) minmax(150px, 1fr) minmax(150px, 1fr) minmax(150px, 1.6fr);
  border-top: 2px solid #b3b3b3;
  border-left: 2px solid #b3b3b3;
  border-right: 2px solid #b3b3b3;
}

.form__table thead,
.form__table tbody,
.form__table tr {
  display: contents;
}

.form__inputs {
  position: relative;
  padding: 70px 120px 70px;
}

.form__table th {
  padding: 10px;
  text-align: left;
  border-bottom: 2px solid #b3b3b3;
}

.form__table th:not(:last-of-type),
.form__table td:not(:last-of-type) {
  position: relative;
}
.form__table th:not(:last-of-type)::before,
.form__table td:not(:last-of-type)::before {
  position: absolute;
  content: "";
  width: 2px;
  height: 100%;
  top: 0;
  right: 0;
  background-color: #b3b3b3;
}

.form__table td {
  padding: 10px;
  border-bottom: 2px solid #b3b3b3;
}

.form__table input,
.form__table select,
.form__table textarea {
  width: 100%;
  border: 1px solid #b3b3b3;
}

.form__table select {
  appearance: none;
}

.form__buttons {
  display: flex;
  justify-content: space-between;
}

.form__buttons-del {
  padding: 4px 2px;
  position: absolute;
  display: block;
  top: 87px;
  right: 35px;
  border-radius: 10px;
  cursor: pointer;
  font-size: 10px;
}

.form__buttons-send,
.form__buttons-add {
  padding: 5px 30px;
  border-radius: 12px;
}

.menu {
  flex-basis: 15%;
  border-right: 5px solid #666666;
}

.menu__list {
  padding: 15px;
  position: relative;
  list-style-type: none;
}
.menu__list::after {
  position: absolute;
  content: "";
  width: 80%;
  height: 2px;
  background-color: #b3b3b3;
  top: 0;
}

.menu__item {
  margin: 0 0 10px 0;
}

.menu h2 {
  margin: 10px 0 0;
  padding: 0 0 0 10px;
}

